<template>
  <div class="row">
    <div class="col-lg-6 col-md-6 col-sm-12">
      <div class="card">
        <div class="card-header">
          <h6 class="title">Sản phẩm</h6>
        </div>
        <div class="card-body form-card">
          <el-upload
            class="upload-demo"
            drag
            :action="host + '/admin/api/import_product'"
            :on-success="handleSuccessProduct"
            accept=".xlsx, .xls, .csv"
          >
            <i class="el-icon-upload"></i>
            <div class="el-upload__text">Drop file here or <em>click to upload</em></div>
            <div class="el-upload__tip" slot="tip">Chấp nhận files xlsx, xls, csv</div>
            <div class="el-upload__tip" slot="tip">
              <a :href="host + '/static/nhap_san_pham.xlsx'" download>File mẫu</a>
            </div>
          </el-upload>
        </div>
      </div>
    </div>
    <div class="col-lg-6 col-md-6 col-sm-12">
      <div class="card">
        <div class="card-header">
          <h6 class="title">Hình ảnh</h6>
        </div>
        <div class="card-body form-card">
          <el-upload
            class="upload-demo"
            drag
            :action="host + '/admin/api/uploadProductImage'"
            :on-success="handleSuccessImage"
            accept=".zip"
          >
            <i class="el-icon-upload"></i>
            <div class="el-upload__text">Drop file here or <em>click to upload</em></div>
            <div class="el-upload__tip" slot="tip">Chấp nhận file zip</div>
          </el-upload>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  import { Message, Button, Upload } from 'element-ui'

  export default{
    components: {
      ElButton: Button,
      ElUpload: Upload
    },
    computed: {
    },
    data () {
      return {
        host: window.BASE_URL
      }
    },
    mounted() {
      this.$store.dispatch('setPageTitle', 'Nhập sản phẩm');
    },
    methods: {
      handleSuccessProduct(response, file, fileList){
        if (response.success){
          Message({
            message: 'Nhập sản phẩm thành công',
            type: 'success'
          });
          this.$router.push({name: 'AllProducts'});
        } else{
          Message({
            message: 'Có lỗi xảy ra',
            type: 'danger'
          });
        }
      },
      handleSuccessImage(response, file, fileList){
        if (!response.code) {
          Message({
            message: 'Nhập hình ảnh thành công',
            type: 'success'
          });
        } else{
          Message({
            message: response.message,
            type: 'danger'
          });
        }
      },
    },
    destroyed(){
      this.$store.dispatch('setCurrentActions', []);
    }
  }
</script>
